let aggresiveActionArr = [];

function aggresiveFireAction(number = 0, name1 = "", name2 = "") {
  aggresiveActionArr = [
    `<span class='member-name'>${name1}</span>   <span class='member-name'>${name2}</span>    `,
    `<span class='member-name'>${name1}</span>   <span class='member-name'>${name2}</span>   ,        <span class='member-name'>${name2}</span>`,
    `<span class='member-name'>${name1}</span>  <span class='member-name'>${name2}</span> :  !      `,
    `<span class='member-name'>${name2}</span>   <span class='member-name'>${name1}</span>      `
  ];
  return aggresiveActionArr[number];
}

aggresiveFireAction();

aggresiveFireAction.caseLength = aggresiveActionArr.length;

export default aggresiveFireAction;
