let AggresiveActionArr = [];

function AggresiveActionDAY(number = 0, name1 = "", name2 = "") {
  AggresiveActionArr = [
    `<span class='member-name'>${name1}</span>   <span class='member-name'>${name2}</span>   `,
    `<span class='member-name'>${name1}</span>  <span class='member-name'>${name2}</span>    `,
    `<span class='member-name'>${name1}</span>   <span class='member-name'>${name2}</span>`,
    `<span class='member-name'>${name1}</span>  <span class='member-name'>${name2}</span> ,   `,
    `<span class='member-name'>${name1}</span>   <span class='member-name'>${name2}</span>, -         `,
    `<span class='member-name'>${name1}</span>  <span class='member-name'>${name2}</span>       `,
    `<span class='member-name'>${name1}</span>    <span class='member-name'>${name2}</span>       `,
    `<span class='member-name'>${name1}</span>  <span class='member-name'>${name2}</span>, : "",       `,
    `<span class='member-name'>${name2}</span>  <span class='member-name'>${name1}</span>   ...   <span class='member-name'>${name1}</span> c $<span class='member-name'>${name2}</span>  , -   `,
    `<span class='member-name'>${name1}</span>  <span class='member-name'>${name2}</span> ...   `,
    `<span class='member-name'>${name1}</span>  "!" <span class='member-name'>${name2}</span>,     ,   `,
    `<span class='member-name'>${name1}</span>   ,         <span class='member-name'>${name2}</span>`
  ];
  return AggresiveActionArr[number];
}

AggresiveActionDAY();

AggresiveActionDAY.caseLength = AggresiveActionArr.length;

export default AggresiveActionDAY;
